@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Regular.woff2") format("woff2"), url("../fonts/Lato-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-SemiBold.woff2") format("woff2"), url("../fonts/Lato-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Bold.woff2") format("woff2"), url("../fonts/Lato-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-ExtraBold.woff2") format("woff2"), url("../fonts/Lato-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.header {
  z-index: 11;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-color: rgba(16, 16, 29, 0.9);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 33px 0 32px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    max-width: unset;
  }
}
.header__logo {
  position: relative;
  display: block;
  width: 247px;
  height: 35px;
  margin-right: 40px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    z-index: 11;
    width: 165px;
    margin-right: 0;
  }
}
.header__logo svg {
  display: block;
  width: 247px;
  height: 35px;
}
@media (max-width: 767px) {
  .header__logo svg {
    width: 165px;
  }
}

.main-nav.is-active .main-nav__toggle::before {
  top: 15px;
  transform: rotate(-45deg) translate(0, -50%);
}
.main-nav.is-active .main-nav__toggle::after {
  top: 13px;
  transform: rotate(45deg) translate(0, 50%);
}
.main-nav.is-active .main-nav__list {
  transform: translateX(0);
}
.main-nav__toggle {
  z-index: 11;
  position: relative;
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: block;
  }
}
.main-nav__toggle::before, .main-nav__toggle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  transition: transform 0.3s ease;
}
.main-nav__toggle::before {
  top: 10px;
}
.main-nav__toggle::after {
  bottom: 10px;
}
@media (hover: hover) {
  .main-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    z-index: 10;
    position: fixed;
    flex-direction: column;
    justify-content: flex-start;
    top: 100px;
    left: 0;
    width: 100%;
    height: 100vh;
    padding: 100px 20px 20px;
    background-color: rgba(16, 16, 29, 0.9);
    transition: transform 0.3s ease;
    transform: translateX(100%);
  }
}
.main-nav__item {
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .main-nav__item {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    margin-right: 0;
    margin-bottom: 30px;
  }
}
.main-nav__item:last-child {
  margin-right: 0;
}
@media (max-width: 767px) {
  .main-nav__item:last-child {
    margin-bottom: 0;
  }
}
.main-nav__link {
  display: block;
  font-weight: 600;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
  transition: opacity 0.3s ease;
  transform: translate3d(0, 0, 0);
}
.main-nav__link.is-active {
  color: #43abf0;
  pointer-events: none;
}
@media (hover: hover) {
  .main-nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link.focus-visible:focus {
  opacity: 0.8;
}

.footer {
  width: 100%;
  background-color: #3d4050;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1170px;
  margin: 0 auto;
  padding: 30px 0;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    flex-direction: column;
  }
}
.footer__copyright {
  position: relative;
  margin: 0;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .footer__copyright {
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .footer__copyright {
    font-size: 12px;
    line-height: 15px;
  }
}
.footer__list {
  position: relative;
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 38px;
  row-gap: 16px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1439px) {
  .footer__list {
    column-gap: 16px;
  }
}
@media (max-width: 1023px) {
  .footer__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    column-gap: 10px;
    row-gap: 10px;
  }
}
.footer__list::before, .footer__list::after {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #c3c3c3;
}
@media (max-width: 1439px) {
  .footer__list::before, .footer__list::after {
    content: none;
  }
}
.footer__list::before {
  left: -50px;
}
.footer__list::after {
  right: -50px;
}
.footer__item {
  display: flex;
  align-items: center;
}
.footer__item svg {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
.footer__text {
  display: block;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #c3c3c3;
}
@media (max-width: 767px) {
  .footer__text {
    font-size: 11px;
    line-height: 14px;
  }
}
.footer__contacts {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 38px;
  row-gap: 16px;
}
@media (max-width: 1439px) {
  .footer__contacts {
    column-gap: 16px;
  }
}
@media (max-width: 1023px) {
  .footer__contacts {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .footer__contacts {
    column-gap: 10px;
    row-gap: 10px;
  }
}
.footer__link {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  color: #ffffff;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 11px;
    line-height: 14px;
  }
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
}
.footer__link svg {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 12px 9px;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  border-radius: 4px;
  background-color: #3e9cdc;
  color: #ffffff;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
  outline: none;
}
.btn--white {
  border: 1px solid #6f6d7b;
  background-color: #ffffff;
  color: #2a2b30;
}
.btn--big {
  padding: 14px 10px;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.intro {
  position: relative;
}
.intro__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.intro__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__wrapper {
  position: relative;
  padding: 203px 0 203px 345px;
}
@media (max-width: 1439px) {
  .intro__wrapper {
    padding: 203px 0;
  }
}
@media (max-width: 1023px) {
  .intro__wrapper {
    padding: 150px 0;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding: 130px 0;
  }
}
.intro__title {
  max-width: 780px;
