  .intro__list {
    margin: 0 0 60px;
  }
}
@media (max-width: 767px) {
  .intro__list {
    margin: 0 0 40px;
  }
}
.intro__item {
  position: relative;
  margin-bottom: 5px;
  padding-left: 25px;
}
@media (max-width: 767px) {
