.intro__item span {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}
@media (max-width: 767px) {
  .intro__item span {
    font-size: 16px;
    line-height: 19px;
  }
}
.intro__buttons {
  display: flex;
  flex-wrap: wrap;
}
.intro__button {
  width: 183px;
  margin-right: 30px;
}
@media (max-width: 767px) {
  .intro__button {
    width: 120px;
    margin-right: 20px;
  }
}
.intro__button:last-child {
  margin-right: 0;
}

.ordering {
