.ordering__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.ordering__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.ordering__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1172px;
  margin: 0 auto;
  padding: 121px 0 151px;
