@media (max-width: 767px) {
  .ordering__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
  }
}
.ordering__title span {
  color: #3e9cdc;
}
.ordering__description {
  margin: 0 0 80px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #bbbbbb;
}
@media (max-width: 1023px) {
  .ordering__description {
    margin: 0 0 60px;
  }
}
@media (max-width: 767px) {
  .ordering__description {
    margin: 0 0 40px;
    font-size: 15px;
    line-height: 19px;
  }
}
.ordering__list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 30px;
  row-gap: 30px;
  width: 100%;
  margin: 0 0 80px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .ordering__list {
    column-gap: 20px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .ordering__list {
    display: flex;
    column-gap: unset;
    row-gap: unset;
    width: calc(100% + 40px);
    margin: 0 -20px;
    padding: 0 20px;
    overflow-x: auto;
  }
}
.ordering__list::-webkit-scrollbar {
  display: none;
}
.ordering__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  min-height: 165px;
}
@media (max-width: 767px) {
  .ordering__item {
    width: 126px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .ordering__item:last-child {
    margin-right: 0;
  }
}
.ordering__circle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  margin-bottom: 20px;
  border: 5px solid #286690;
  border-radius: 50%;
  background-color: #3e9cdc;
}
@media (max-width: 1023px) {
  .ordering__circle {
    width: 70px;
    height: 70px;
  }
}
.ordering__circle svg {
  width: 55px;
  height: 55px;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .ordering__circle svg {
    width: 40px;
    height: 40px;
  }
}
.ordering__text {
  display: block;
  max-width: 192px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 1023px) {
  .ordering__text {
    font-size: 13px;
    line-height: 16px;
  }
}
.ordering__points {
  position: absolute;
  display: grid;
  grid-template-columns: repeat(5, 6px);
  column-gap: 20px;
  align-items: center;
  top: 47px;
  right: -69px;
}
@media (max-width: 1439px) {
  .ordering__points {
    column-gap: 20px;
    right: -83px;
  }
}
@media (max-width: 1023px) {
  .ordering__points {
    column-gap: 15px;
    top: 33px;
    right: -66px;
  }
}
.ordering__point {
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #c4c4c4;
}
@media (max-width: 1439px) {
  .ordering__point--fifth {
    display: none;
  }
}
.ordering__form {
  width: 100%;
}
.ordering__form form {
  display: flex;
  flex-direction: column;
}
.ordering__form-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .ordering__form-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767px) {
  .ordering__form-wrapper {
    grid-template-columns: 1fr;
  }
}
.ordering__range-slider {
  grid-column: 1/3;
}
@media (max-width: 767px) {
  .ordering__range-slider {
    grid-column: 1/2;
  }
}
.ordering__range-slider label {
  display: flex;
  align-items: center;
}
.ordering__range-slider input[type=range] {
  width: 100%;
  height: 6px;
  border: 1px solid #ffffff;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.7);
  -webkit-appearance: none;
}
.ordering__range-slider input[type=range]::-webkit-slider-thumb {
  width: 24px;
  height: 24px;
  border: 3px solid #286690;
  border-radius: 50%;
  background-color: #42a9ed;
  cursor: grab;
  -webkit-appearance: none;
}
.ordering__range-slider input[type=range]::-moz-range-thumb {
  width: 24px;
  height: 24px;
  border: 3px solid #286690;
  border-radius: 50%;
  background-color: #42a9ed;
  cursor: grab;
  -webkit-appearance: none;
}
.ordering__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.ordering__label {
  display: block;
  margin-right: 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .ordering__label {
    font-size: 16px;
    line-height: 19px;
  }
}
.ordering__number {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  white-space: nowrap;
  color: #ffffff;
}
@media (max-width: 767px) {
  .ordering__number {
    font-size: 16px;
    line-height: 19px;
  }
}
.ordering__button {
  width: 100%;
  max-width: 370px;
  margin: 0 auto;
}

.custom-select {
  position: relative;
  width: 100%;
}
.custom-select__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 12px 10px;
  border: 1px solid #ffffff;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.85);
  transition: border 0.3s ease, border-radius 0.3s ease, background-color 0.3s ease;
  cursor: pointer;
}
.custom-select__text {
  display: block;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #272733;
  transition: color 0.3s ease;
}
.custom-select__icon {
  transition: transform 0.3s ease;
}
.custom-select__icon svg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  color: #000000;
  transition: color 0.3s ease;
}
.custom-select__dropdown {
  z-index: 20;
  position: absolute;
  display: none;
  top: 100%;
  width: 100%;
  border: 1px solid #3d4050;
  border-radius: 3px;
  background-color: rgba(39, 39, 51, 0.9);
  transition: opacity 0.3s ease, visibility 0.3s ease;
  opacity: 0;
  visibility: hidden;
}
.custom-select__list {
  width: 100%;
  max-height: 180px;
  margin: 0;
  padding: 4px 10px;
  overflow-y: auto;
  border-top: 2px solid rgba(255, 255, 255, 0);
  border-right: 4px solid rgba(255, 255, 255, 0);
  border-bottom: 2px solid rgba(255, 255, 255, 0);
  list-style: none;
  scrollbar-width: thin;
  scrollbar-color: #3e9cdc #3d4050;
}
.custom-select__list::-webkit-scrollbar {
  width: 7px;
}
.custom-select__list::-webkit-scrollbar-track {
  border-radius: 3px;
  background-color: #3d4050;
}
.custom-select__list::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: #3e9cdc;
}
.custom-select__item {
  font-weight: 400;
  font-size: 18px;
  line-height: 34px;
  color: #ffffff;
  cursor: pointer;
}
.custom-select__item.is-active {
  color: #42a9ed;
}
.custom-select.is-open .custom-select__button {
  border: 1px solid rgba(61, 64, 80, 0.85);
  border-radius: 3px 3px 0 0;
  background-color: rgba(61, 64, 80, 0.85);
}
.custom-select.is-open .custom-select__text {
  color: #ffffff;
}
.custom-select.is-open .custom-select__dropdown {
  display: block;
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg) translateZ(0);
}
.custom-select.is-open .custom-select__icon svg {
  color: #ffffff;
}
.custom-select input {
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  position: absolute;
  width: 1px;
}

.custom-input {
  width: 100%;
}
.custom-input--upload .custom-input__label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
  border: 2px solid #3e9cdc;
  border-radius: 3px;
  background-color: #ffffff;
  color: #272733;
  transition: opacity 0.3s ease;
  cursor: pointer;
}
@media (hover: hover) {
  .custom-input--upload .custom-input__label:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.custom-input--upload svg {
  width: 18px;
  height: 20px;
  margin-right: 10px;
}
.custom-input--upload input {
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  position: absolute;
  width: 1px;
}
.custom-input label {
  width: 100%;
}
.custom-input input {
  width: 100%;
  padding: 12px 10px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  border: 1px solid #ffffff;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.85);
  color: #272733;
  transition: opacity 0.3s ease;
  outline: 0;
}
.custom-input input::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #272733;
}
@media (hover: hover) {
  .custom-input input:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}